* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "League Spartan", sans-serif;
  font-size: 0.9375rem;
  color: hsl(303deg, 10%, 53%);
  background-image: url(../img/bg-pattern-top-mobile.svg), url(../img/bg-pattern-bottom-mobile.svg);
  background-repeat: no-repeat, no-repeat;
  background-position: top center, center bottom;
}

@media (min-width: 770px) {
  body {
    background-image: url(../img/bg-pattern-top-desktop.svg), url(../img/bg-pattern-bottom-desktop.svg);
    background-position: top left, bottom right;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.card {
  padding-inline: 1.875rem;
  padding-block: 5.625rem;
}
.card__heading {
  text-align: center;
  color: hsl(300deg, 43%, 22%);
  font-size: 2.5rem;
  font-weight: 700;
}
.card__text {
  margin-top: 1.25rem;
  line-height: 1.2;
  font-size: 1.125rem;
  font-weight: 500;
  text-align: center;
}

.wrapper {
  margin-top: 1.875rem;
  display: grid;
  grid-auto-flow: row;
  gap: 0.9375rem;
}
.wrapper__stars {
  background-color: hsl(300deg, 24%, 96%);
  padding: 1.25rem;
  text-align: center;
  border-radius: 0.625rem;
}
.wrapper__heading {
  margin-top: 0.625rem;
  color: hsl(300deg, 43%, 22%);
  font-size: 1rem;
  font-weight: 700;
}

.testimonials {
  margin-top: 3.75rem;
  display: grid;
  grid-auto-flow: row;
  gap: 0.9375rem;
}
.testimonials__card {
  padding: 30px;
  border-radius: 0.625rem;
  background-color: hsl(300deg, 43%, 22%);
  color: hsl(0deg, 0%, 100%);
  line-height: 1.4;
}
.testimonials__card--wrapper {
  display: flex;
  align-items: center;
}
.testimonials__img {
  display: block;
  border-radius: 3.125rem;
  width: 2.5rem;
  margin-right: 1.5625rem;
}
.testimonials__name {
  font-size: 1rem;
  line-height: 1.1;
  font-weight: 500;
}
.testimonials__verified {
  color: hsl(333deg, 80%, 67%);
  font-size: 1rem;
}
.testimonials__paragraph {
  margin-top: 25px;
  font-size: 1rem;
}

@media (min-width: 768px) {
  .card {
    padding-top: 0;
    padding-bottom: 0;
    padding-inline: 0;
    width: 80%;
  }
  .card__contain {
    display: flex;
    justify-content: space-between;
  }
  .card__wrapper-text {
    max-width: 40%;
  }
  .card__heading {
    font-size: 50px;
    max-width: 80%;
    text-align: left;
  }
  .card__text {
    text-align: left;
    font-size: 20px;
  }
  .wrapper {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
  }
  .wrapper__stars {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 130%;
    padding: 0;
  }
  .wrapper__stars:nth-child(1) {
    grid-column: 1/7;
    grid-row: 1/2;
  }
  .wrapper__stars:nth-child(2) {
    grid-column: 2/8;
    grid-row: 2/3;
  }
  .wrapper__stars:nth-child(3) {
    grid-column: 3/9;
    grid-row: 3/4;
  }
  .wrapper__heading {
    margin: 0;
  }
  .testimonials {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(11, 1fr);
    gap: 20px;
    margin-top: 60px;
  }
  .testimonials__card:nth-child(1) {
    grid-row: 1/10;
    grid-column: 1/2;
  }
  .testimonials__card:nth-child(2) {
    grid-row: 2/11;
    grid-column: 2/3;
  }
  .testimonials__card:nth-child(3) {
    grid-row: 3/12;
    grid-column: 3/4;
  }
  .testimonials__name {
    font-size: 20px;
  }
}